.menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;
    position: relative;
    overflow: hidden;

    &:hover {
      cursor: pointer;
    }

    & .imageHolder {
      position: absolute;
      height: 100%;
      width: 100%;
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
      backface-visibility: hidden;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
    }

    &:hover .imageHolder {
      transform: scale(1.2);
    }

   
    
    &.large {
        height: 380px;
    }

    &:first-child {
      margin-right: 7.5px;
    }
  
    &:last-child {
      margin-left: 7.5px;
    }
  
    .content {
      height: 90px;
      padding: 0 25px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border: 1px solid black;
      background-color: white;
      opacity: 0.7;

      & h1 {
        text-transform: uppercase;
      }
  
      .title {
        font-weight: bold;
        margin-bottom: 6px;
        font-size: 22px;
        color: #4a4a4a;
      }
  
      .subtitle {
        font-weight: lighter;
        font-size: 16px;
      }
    }
  }
